
import {
    createHashRouter,
  } from "react-router-dom";
import Community from "../views/community";
import Main from "../views/main";
import Notes from "../views/notes";
  const router = createHashRouter([
    {
      path: "/",
      element: <Main />,
  
      },
      {
        path: "/notes",
        element: <Notes />,
    
      },
      {
        path: "/community",
        element: <Community />,
    
      },
  ]);
  
  export default router;
  