
import React from 'react';
import { Menu  } from 'antd';
import { useNavigate } from 'react-router-dom';
import "./index.scss"
function Tab ()
{
    const navigate=useNavigate()
    const onChange = (e) => {
        navigate(e.key)
        console.log(e.key);
      };
      const items = [
        {
          path: '/',
          label: '',
        },
        {
          path: '/notes',
          label: '',
        },
        {
          path: '/community',
          label: '',
        },
    ];
    return (
        <Menu onClick={onChange} mode={'horizontal'} className='menu'>
        {items.map(item => (
            <Menu.Item key={ item.path }>{item.label }</Menu.Item>
        ))}
      </Menu>)
}
export default Tab