import Tab from "../../componet/tab";
import React from 'react';
import { Collapse } from 'antd';
import { useEffect,useState } from 'react'
import axios from 'axios'
import "./index.scss"

function Community ()
{

  const [items,setItems]=useState('')
  useEffect(() => {  
    axios.get('http://localhost:8080/webapi/notes/list').then(response => {  
      const res = response.data.data.map( item =>
      {

          return {
            label: item.title,
            children: <div style={ { wordWrap:" break-word"} }  dangerouslySetInnerHTML={{ __html: item.content }}></div>
        }
      });  
      console.log(response);
      setItems( res );  
      console.log(res);
    })
  }, []); //   
    return (
        <div><Tab></Tab>
            <div className="main">
          <Collapse accordion  items={ items } />
        </div>

        </div>
        
    )
}
export default Community